import React, { useEffect, useContext } from "react";
import "../assets/products/products.css";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import productContext from "../context/Products/productContext";
import { LazyLoadImage } from "react-lazy-load-image-component";
import TagManager from "react-gtm-module";
// import _default from "concurrently";
const tagManagerArgs = {
  gtmId: "GTM-5JNKTJW",
};
TagManager.initialize(tagManagerArgs);
export default function Products(props) {
  const api = `https://powerful-headland-67436.herokuapp.com/api/notes/fetchProducts/${props.limit}`;
  let context = useContext(productContext);
  const { get, setProducts, products } = context;

  let navigate = useNavigate();
  const productPage = (e) => {
    // window.scrollTo(0, 0);
    document.querySelector("body").scrollTo(0, 0);
    const iD = e.currentTarget.id;
    // window.location.assign(`/product-detail/${id}`);
    navigate(`/product-detail/${iD}`);
    get(iD);
  };

  const getPhotos = async () => {
    props.setLoading(true);
    const resp = await fetch(
      // `https://api.pexels.com/v1/search/?page=${props.page}&per_page=${props.perPage}&query=${props.query}`,
      api,
      {
        headers: {
          "auth-token":
            "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYyNTQ2MzY0ZjUxY2FmNzBlMTIzMWEwOSIsImlhdCI6MTY0OTc3NzY0Nn0.h3vGm3RtPg6lPJNjCvBHj-oH876nuyzoUfJqzDOKGiE",
          // Authorization: props.api,
        },
      }
    );
    const resData = await resp.json();
    // setProducts(resData.photos);
    // console.log(resData);
    setProducts(resData);
    props.setLoading(false);
  };
  useEffect(() => {
    getPhotos();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  // console.log(products);s
  return (
    <>
      <section className="products">
        <h1>{props.heading}</h1>
        <p>We deliver best packaging for you.</p>
        <div className="productCol">
          {products.map((item) => {
            // const { alt, id, src } = item;
            const { name, _id, price, url } = item;
            return (
              <div
                className="box"
                key={_id}
                id={_id}
                onClick={(e) => {
                  productPage(e);
                  getPhotos();
                  window.dataLayer.push({
                    id: _id,
                    "product-name": name,
                  });
                }}
              >
                <div className="imgContainer">
                  <LazyLoadImage src={url} effect="blur" alt={name} />
                </div>
                <div className="content">
                  <h5>{name}</h5>

                  <p>
                    Mulai Dari Rp.
                    <span> {price}</span>
                    <Link
                      to={`/product-detail/${_id}`}
                      style={{ display: "none" }}
                      className="Mproduct"
                    >
                      View More Product
                    </Link>
                  </p>
                </div>
              </div>
            );
          })}
        </div>
        <Link to="/more-products" className="Mproduct">
          View More Product
        </Link>
      </section>
    </>
  );
}

Products.defaultProps = {
  query: "packaging",
  limit: 9,
};
